<template>
  <el-card>
    <div slot="header" class="clearfix">
      <el-row>
        <el-col :span="8" :offset="4">
          <div class="block">
            <span class="demonstration">日期:</span>
            <el-date-picker
              v-model="value1"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            >
            </el-date-picker>
          </div>
        </el-col>
        <el-col :span="4">
          <!-- <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="炉号："> -->
          <label>炉号：</label
          ><el-input
            v-model="nNo"
            @change="clickSearch"
            style="width: 50%"
          ></el-input>
          <!-- </el-form-item>
        </el-form> -->
        </el-col>
        <el-col :span="4">
          <el-button
            :style="{ display: buttonVisible }"
            type="primary"
            @click="clickSearch"
            >检索</el-button
          >
          <el-button
            :style="{ display: buttonVisible }"
            type="primary"
            @click="clickExport"
            >导出</el-button
          >
        </el-col>
      </el-row>
    </div>
    <el-row>
      <el-col :span="12">
          <el-table
            :data="tableDataEnd"
            style="width: 100%"
          >
            <el-table-column prop="date" label="日期"> </el-table-column>
            <el-table-column prop="name" label="炉号" width="100"> </el-table-column>
            <el-table-column prop="address" label="温度" width="150"> </el-table-column>
            <el-table-column prop="maxTemp" label="上限温度" width="150"> </el-table-column>
            <el-table-column prop="minTemp" label="下限温度" width="150"> </el-table-column>
            <el-table-column prop="flag" label="缺铝报警" width="100"> </el-table-column>
          </el-table>
          <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="pageSize"
      layout="total,sizes,prev,pager,next,jumper"
      :total="totalItems"
    >
    </el-pagination>
      </el-col>
      <el-col :span="12">
        <div id="echarts_box" style="height:450px; width: 100%"></div>
      </el-col>
    </el-row>
  </el-card>
</template>

<script>
import Request from "../utils/axiosUtils";
import axios from "axios";
import echarts from "echarts";
import moment from "moment";
export default {
  props: {
    className: {
      type: String,
      default: "yourClassName",
    },
    id: {
      type: String,
      default: "yourID",
    },
    width: {
      type: String,
      default: "500px",
    },
    height: {
      type: String,
      default: "500px",
    },
  },
  name: "dataSearch",
  data() {
    return {
      value1: "",
      startDate: "",
      endDate: "",
      form: {},
      nNo: "",
      buttonVisible: "",
      chart: null,
      tableData: [
        // {
        //   date: '2016-05-02',
        //   name: '01',
        //   address: '650',
        //   flag: 0
        // },
      ],
      tableDataName: "",
      tableDataEnd: [],
      currentPage: 4,
      pageSize: 10,
      totalItems: 0,
      filterTableDataEnd: [],
      flag: false,
    };
  },
  mounted() {
    let nowDate = new Date();
    let year = nowDate.getFullYear();
    let month = nowDate.getMonth() + 1;
    let day = nowDate.getDate() + 1;
    let endTime = `${year}-${month}-${day}`;
    this.nowTime = endTime; // 当前的时间点
    let befDate = new Date(nowDate.getTime() - 7 * 24 * 3600 * 1000);
    let byear = befDate.getFullYear();
    let bmonth = befDate.getMonth() + 1;
    let bday = befDate.getDate();
    let startTime = `${byear}-${bmonth}-${bday}`;
    this.weekBeforeTime = startTime; // 向前推迟一周的时间点

    // 默认日期赋值
    this.value1 = [
      new Date(byear + ", " + bmonth + ", " + bday),
      new Date(year + ", " + month + ", " + day),
    ];
    this.nNo = "1";
    //echarts图表
    this.chartLine = echarts.init(document.getElementById("echarts_box"));

    const guestAuthority = this.$cookies.get("guestAuthority");
    if (guestAuthority == undefined) {
      this.$router.push("/LoginPage");
    } else if (guestAuthority == 98) {
      this.buttonVisible = "none";
    }

    //this.initChart();
  },
  methods: {
    clickSearch() {
      if (this.value1 == "") return;
      this.startDate = this.value1[0];
      this.endDate = this.value1[1];
      // let max = 0;
      // let min = 0;
      // Request.get("/api/getFurnaceTemperature?furnaceId="+this.nNo+"&furnaceType=0"
      // ).then((res)=>{
      //   if(res.data.resCode!=0) return;
      //   max = res.data.message.maxTemperature;
      //   min = res.data.message.minTemperature;
      // }
      // );
      Request.post("/api/searchLog", {
        startDate: moment(this.startDate).format("YYYY-MM-DD HH:mm:ss"),
        endDate: moment(this.endDate).format("YYYY-MM-DD HH:mm:ss"),
        furnaceId: this.nNo,
        Offset: 0,
        limit: 0,
      }).then((res) => {
        console.log(res);
        if(res.data.resCode!=0) return;
        const mesArray = res.data.message.logData.map(
          ({
            timestamp: date,
            furnaceId: name,
            currentTemperature: address,
            maxTemperature: maxTemp,
            minTemperature: minTemp,
            status: flag,
            operationType,
          }) => ({ date, name, address, maxTemp, minTemp, flag, operationType })
        );
        
        // for(let idx=0;idx<mesArray.length;idx++)
        // {
          
        //   if(mesArray[idx].operationType==1){
        //     mesArray[idx].maxTemp = max;
        //     mesArray[idx].minTemp = min;
        //   }else if(mesArray[idx].operationType==2)
        //   {
        //     max = mesArray[idx].maxTemp;
        //     min = mesArray[idx].minTemp;
        //   }
        // }
        mesArray.filter((item)=>{console.log(item.operationType); return item.operationType == 1;});

        const chartArray = mesArray;
        
          
        var option = {
          tooltip: {
            //设置tip提示
            trigger: "axis",
          },

          legend: {
            //设置区分（哪条线属于什么）
            data: ["温度"],
          },
          color: ["#8AE09F"], //设置区分（每条线是什么颜色，和 legend 一一对应）
          xAxis: {
            //设置x轴
            type: "category",
            boundaryGap: false, //坐标轴两边不留白
            data: mesArray.map(({ date }) => date),
            name: "日期", //X轴 name
            nameTextStyle: {
              //坐标轴名称的文字样式
              color: "#FA6F53",
              fontSize: 16,
              padding: [0, 0, 0, 20],
            },
            axisLine: {
              //坐标轴轴线相关设置。
              lineStyle: {
                color: "#FA6F53",
              },
            },
          },
          yAxis: {
            name: "温度",
            nameTextStyle: {
              color: "#FA6F53",
              fontSize: 16,
              padding: [0, 0, 10, 0],
            },
            axisLine: {
              lineStyle: {
                color: "#FA6F53",
              },
            },
            type: "value",
          },
          series: [
            {
              name: "当前温度",
              data: mesArray.map(
                ({ address }) => address
              ),
              type: "line", // 类型为折线图
              lineStyle: {
                // 线条样式 => 必须使用normal属性
                normal: {
                  color: "#8AE09F",
                },
              },
            },
            {
              name: "上限温度",
              data: mesArray.map(
                ({  maxTemp }) => maxTemp
              ),
              type: "line", // 类型为折线图
              lineStyle: {
                // 线条样式 => 必须使用normal属性
                normal: {
                  color: "#FF0000",
                },
              },
            },
            {
              name: "下限温度",
              data: mesArray.map(
                ({  minTemp }) => minTemp
              ),
              type: "line", // 类型为折线图
              lineStyle: {
                // 线条样式 => 必须使用normal属性
                normal: {
                  color: "#0000FF",
                },
              },
            },
          ],
        };

        // 使用刚指定的配置项和数据显示图表。
        this.chartLine.setOption(option);

        this.tableData = mesArray.reverse();

        this.tableDataEnd = [];
          this.currentPage = 1;
          this.totalItems = this.tableData.length;
          this.currentChangePage(this.tableData);
          this.flag = true;
      });
    },
   handleSizeChange(val) {
      this.pageSize = val;
      this.handleCurrentChange(1);
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.currentChangePage(this.tableData);
      console.log(this.tableDataEnd);
      
    },
    currentChangePage(list) {
      let from = (this.currentPage - 1) * this.pageSize;
      let to = this.currentPage * this.pageSize;
      this.tableDataEnd = [];
      for (; from < to; from++) {
        if (list[from]) this.tableDataEnd.push(list[from]);
      }
    },

    clickExport() {
      if (this.value1 == "") return;
      this.startDate = this.value1[0];
      this.endDate = this.value1[1];
      Request.post("/api/saveExcel", {
        startDate: moment(this.startDate).format("YYYY-MM-DD HH:mm:ss"),
        endDate: moment(this.endDate).format("YYYY-MM-DD HH:mm:ss"),
      }).then((res) => {
        
        if (res.data.message != "") {
          var timer = null;
            timer = setInterval(() => {
              let downloadId = res.data.message;
              Request.post("/api/checkDownloadId", {
                downloadId: downloadId,
              }).then((res) => {
                if (res.data.message == 1) {
                  clearInterval(timer);
                  axios
                    .get("/api/downloadExcel?downloadId=" + downloadId, {
                      responseType: "blob",
                    })
                    .then((res) => {
                      console.log(res);
                      if (!res) {
                        return;
                      }

                      let url = window.URL.createObjectURL(res.data);
                      let link = document.createElement("a");
                      //link.style.display = 'none'
                      link.href = url;
                      link.setAttribute("download", "数据检索.xls");
                      //link.download = "download.xls";
                      document.body.appendChild(link);
                      link.click();

                      
                    })
                    .catch((res) => {
                      //
                    });

                }
              });
            }, 1500);
          
        }
      });
    },
    initChart() {
      // 2. 发起请求，获取数据
      this.chartline.setOption({
        tooltip: {
          //设置tip提示
          trigger: "axis",
        },

        legend: {
          //设置区分（哪条线属于什么）
          data: ["温度"],
        },
        color: ["#8AE09F"], //设置区分（每条线是什么颜色，和 legend 一一对应）
        xAxis: {
          //设置x轴
          type: "category",
          boundaryGap: false, //坐标轴两边不留白
          data: ["2020-12-20 1:1:1"],
          name: "日期", //X轴 name
          nameTextStyle: {
            //坐标轴名称的文字样式
            color: "#FA6F53",
            fontSize: 16,
            padding: [0, 0, 0, 20],
          },
          axisLine: {
            //坐标轴轴线相关设置。
            lineStyle: {
              color: "#FA6F53",
            },
          },
        },
        yAxis: {
          name: "温度",
          nameTextStyle: {
            color: "#FA6F53",
            fontSize: 16,
            padding: [0, 0, 10, 0],
          },
          axisLine: {
            lineStyle: {
              color: "#FA6F53",
            },
          },
          type: "value",
        },
        series: [
          {
            name: "当前温度",
            data: [0],
            type: "line", // 类型为折线图
            lineStyle: {
              // 线条样式 => 必须使用normal属性
              normal: {
                color: "#FF0000",
              },
            },
          },
        ],
      });
    },
  },
};
</script>

<style scoped>
</style>



