<template>
  <el-card>
    <div slot="header" class="clearfix">
      <el-row>
        <el-col :span="8" :offset="4">
          <div class="block">
            <span class="demonstration">日期:</span>

            <el-date-picker
              v-model="value1"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            >
            </el-date-picker>
          </div>
        </el-col>

        <el-col :span="4">
          <!-- <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="炉号："> -->
          <label>炉号：</label
          ><el-input
            v-model="nNo"
            @change="clickSearch"
            style="width: 50%"
          ></el-input>
          <!-- </el-form-item>
        </el-form> -->
        </el-col>
        <el-col :span="4">
          <el-button
            :style="{ display: buttonVisible }"
            type="primary"
            @click="clickSearch"
            >检索</el-button
          >
          <el-button
            :style="{ display: buttonVisible }"
            type="primary"
            @click="clickExport"
            >导出</el-button
          >
        </el-col>
      </el-row>
    </div>
    <el-table
      :data="tableDataEnd"
      style="width: 100%"
    >
      <el-table-column prop="date" label="日期"> </el-table-column>
      <el-table-column prop="name" label="炉号"> </el-table-column>
      <el-table-column prop="operateString" label="操作"> </el-table-column>
      <!--
      <el-table-column
        prop="targettemp"
        label="目标温度">
      </el-table-column>
      <el-table-column
        prop="maxtemp"
        label="最大温度">
      </el-table-column>
      <el-table-column
        prop="mintemp" 
        label="最小温度">
      </el-table-column>
-->
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="pageSize"
      layout="total,sizes,prev,pager,next,jumper"
      :total="totalItems"
    >
    </el-pagination>
  </el-card>
</template>

<script>
import Request from "../utils/axiosUtils";
import axios from "axios";
import moment from "moment"
export default {
  props: {
    className: {
      type: String,
      default: "yourClassName",
    },
    id: {
      type: String,
      default: "yourID",
    },
    width: {
      type: String,
      default: "500px",
    },
    height: {
      type: String,
      default: "500px",
    },
  },
  name: "operateSearch",
  data() {
    return {
      timer: "",
      value1: "",
      startDate: "",
      endDate: "",
      nNo: "",
      buttonVisible: "",
      tableData: [],
      form: {},
      tableDataName: "",
      tableDataEnd: [],
      currentPage: 4,
      pageSize: 10,
      totalItems: 0,
      filterTableDataEnd: [],
      flag: false,
    };
  },
  mounted() {
    let nowDate = new Date();
    nowDate = new Date(nowDate.getTime() + 23 * 3600 * 1000);
    let year = nowDate.getFullYear();
    let month = nowDate.getMonth() + 1;
    let day = nowDate.getDate();
    let endTime = `${year}-${month}-${day}`;
    this.nowTime = endTime; // 当前的时间点
    let befDate = new Date(nowDate.getTime() - 7 * 24 * 3600 * 1000);
    let byear = befDate.getFullYear();
    let bmonth = befDate.getMonth() + 1;
    let bday = befDate.getDate();
    let startTime = `${byear}-${bmonth}-${bday}`;
    this.weekBeforeTime = startTime; // 向前推迟一周的时间点

    // 默认日期赋值
    this.value1 = [
      new Date(byear + ", " + bmonth + ", " + bday),
      new Date(year + ", " + month + ", " + day),
    ];

    const guestAuthority = this.$cookies.get("guestAuthority");
    if (guestAuthority == undefined) {
      this.$router.push("/LoginPage");
    } else if (guestAuthority == 98) {
      this.buttonVisible = "none";
    }
  },
  creater() {
    this.totalItems = this.tableData.length;
    if (this.totalItems > this.pageSize) {
      for (let index = 0; index < this.pageSize; index++) {
        this.tableDataEnd.push(this.tableData[index]);
      }
    } else {
      this.tableDataEnd = this.tableData;
    }
  },
  methods: {
    clickSearch() {
      if (this.value1 == "") return;
      this.startDate = this.value1[0];
      this.endDate = this.value1[1];
      if (this.nNo == null) {
        
        Request.post("/api/searchOperationLog", {
          startDate: moment(this.startDate).format("YYYY-MM-DD HH:mm:ss"),
          endDate: moment(this.endDate).format("YYYY-MM-DD HH:mm:ss"),
        }).then((res) => {
          console.log(res);
          // const mesArray=res.data.message.map(({timestamp:date, furnaceId:name, operationtype:operatetype,targetTemperature:targettemp
          //       ,maxTemperature:maxtemp,minTemperature:mintemp})=>
          //  ({date,name,operatetype,targettemp,maxtemp,mintemp})
          const mesArray = res.data.message.map(
            ({
              timestamp: date,
              furnaceId: name,
              operationType: operateType,
              targetTemperature: targettemp,
              maxTemperature: maxtemp,
              minTemperature: mintemp,
            }) => {
              //  let data = res.data.message.timestamp;
              //   let name = res.data.message.furnaceId;
              if (operateType == 2) {
                let operateString =
                  "前端设置:" +
                  targettemp +
                  ", 最大最小：[" +
                  maxtemp +
                  "," +
                  mintemp +
                  "];";
                return { date, name, operateString };
              } else if (operateType == 3) {
                let operateString = "温控器设置:" + targettemp + ";";
                return { date, name, operateString };
              }
            }
          );
          const abc = mesArray.filter((ele) => typeof ele !== "undefined");

          this.tableData = abc.reverse();
          this.tableDataEnd = [];
          this.filterTableDataEnd = [];
          this.tableData.forEach((value, index) => {
            if (value.name) {
              if (value.name.indexOf(this.tableDataName) >= 0) {
                this.filterTableDataEnd.push(value);
              }
            }
          });
          this.currentPage = 1;
          this.totalItems = this.tableData.length;
          this.currentChangePage(this.tableData);
          this.flag = true;
        });
      } else {
        console.log(this.startDate,this.endDate);
        Request.post("/api/searchOperationLog", {
          startDate: moment(this.startDate).format("YYYY-MM-DD HH:mm:ss"),
          endDate: moment(this.endDate).format("YYYY-MM-DD HH:mm:ss"),
          furnaceId: this.nNo,
        }).then((res) => {
          console.log(res);
          // const mesArray=res.data.message.map(({timestamp:date, furnaceId:name, operationtype:operatetype,targetTemperature:targettemp
          //       ,maxTemperature:maxtemp,minTemperature:mintemp})=>
          //  ({date,name,operatetype,targettemp,maxtemp,mintemp})
          const mesArray = res.data.message.map(
            ({
              timestamp: date,
              furnaceId: name,
              operationType: operateType,
              targetTemperature: targettemp,
              maxTemperature: maxtemp,
              minTemperature: mintemp,
            }) => {
              //  let data = res.data.message.timestamp;
              //   let name = res.data.message.furnaceId;
              if (operateType == 2) {
                let operateString =
                  "前端设置:" +
                  targettemp +
                  ", 最大最小：[" +
                  maxtemp +
                  "," +
                  mintemp +
                  "];";
                return { date, name, operateString };
              } else if (operateType == 3) {
                let operateString = "温控器设置:" + targettemp + ";";
                return { date, name, operateString };
              }
            }
          );
          const abc = mesArray.filter((ele) => typeof ele !== "undefined");

          this.tableData = abc.reverse();
          this.tableDataEnd = [];
          this.filterTableDataEnd = [];
          this.tableData.forEach((value, index) => {
            if (value.name) {
              if (value.name.indexOf(this.tableDataName) >= 0) {
                this.filterTableDataEnd.push(value);
              }
            }
          });
          this.currentPage = 1;
          this.totalItems = this.tableData.length;
          this.currentChangePage(this.tableData);
          this.flag = true;
        });
      }
    },

    handleSizeChange(val) {
      this.pageSize = val;
      this.handleCurrentChange(1);
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      if (!this.flag) {
        this.currentChangePage(this.tableData);
        console.log(this.tableDataEnd);
      } else {
        this.currentChangePage(this.filterTableDataEnd);
        console.log(this.tableDataEnd);
      }
    },
    currentChangePage(list) {
      let from = (this.currentPage - 1) * this.pageSize;
      let to = this.currentPage * this.pageSize;
      this.tableDataEnd = [];
      for (; from < to; from++) {
        if (list[from]) this.tableDataEnd.push(list[from]);
      }
    },
    clickExport() {
      if (this.value1 == "") return;
      this.startDate = this.value1[0];
      this.endDate = this.value1[1];
      Request.post("/api/saveOperationExcel", {
        startDate: moment(this.startDate).format("YYYY-MM-DD HH:mm:ss"),
        endDate: moment(this.endDate).format("YYYY-MM-DD HH:mm:ss"),
        furnaceId: this.nNo,
      }).then((res) => {
        
        if (res.data.message != "") {
          var timer = null;
            timer = setInterval(() => {
              let downloadId = res.data.message;
              Request.post("/api/checkDownloadId", {
                downloadId: downloadId,
              }).then((res) => {
                if (res.data.message == 1) {
                  clearInterval(timer);
                  axios
                    .get("/api/downloadExcel?downloadId=" + downloadId, {
                      responseType: "blob",
                    })
                    .then((res) => {
                      console.log(res);
                      if (!res) {
                        return;
                      }

                      let url = window.URL.createObjectURL(res.data);
                      let link = document.createElement("a");
                      //link.style.display = 'none'
                      link.href = url;
                      link.setAttribute("download", "操作查询.xls");
                      //link.download = "download.xls";
                      document.body.appendChild(link);
                      link.click();

                    })
                    .catch((res) => {
                      //
                    });

                }
              });
            }, 1500);
          
        }
      });
    },
  },
};
</script>

<style scoped>
</style>



