<template>
  <div class="mainChar">
    <el-card style="margin: 20px">
      <div slot="header" class="clearfix">

        <span style="font-weight: 600;font-size: 45px">监控状态</span>
        <!--  <el-button style="float: right; padding: 3px 0;font-size: 20px" type="text" @click="clickSearch">检索</el-button>-->
      </div>
      <!--<input v-model="123"></input>-->
      <!--
      <el-card class="cardCls">
        <el-row>
          <el-col :span="6" v-for="(item,index) in quantitativeFurnaceArray" :key="index">
            <div>
              <h3>{{ item.number }}号定量炉</h3>
              <el-row style="text-align: center">
                <el-col :span="12">
                  <el-progress type="dashboard" :percentage="parseInt((item.quality/15).toFixed(1))" :color="colors"
                               :stroke-width="15"
                               style="margin-top: 70px"></el-progress>
                  <span>{{ item.quality }}KG</span>
                </el-col>
                <el-col :span="12">
                  <vue-thermometer
                    :value=item.temperature
                    :min="0"
                    :max="1000"
                    :options="getTemStyleOptions(item.temperature)"
                  />
                  <span>{{ item.temperature }}°C</span>
                </el-col>

              </el-row>
              <el-button type="primary" @click="clickSetTemperature(item.number)">设定</el-button>

            </div>
          </el-col>
        </el-row>
      </el-card>
-->
      <el-card class="cardCls">

        <el-row>
          <el-col :span="6" v-for="(item,index) in HoldingfurnaceArray" :key="index">
            <div>
              <h3 class="temperfontsize">{{ item.number }}号保温炉</h3>
              <el-row style="text-align: center">
                <el-col :span="12">
                  <div class="furnaceStatusCls" :class="item.status?'exceptionColor':'normalColor'"></div>
                  <span class="statusfont">状态</span>

                </el-col>
                <el-col :span="12">
                  <testTheremo
                    :value=item.temperature
                    :min=item.minTem-20
                    :max=item.maxTem+20
                    :options="getTemStyleOptions(item.temperature,item.maxTem,item.minTem)"
                  />
                  <span class="temperfontsize">{{ item.temperature }}°C</span>
                </el-col>

              </el-row>
              <el-button type="primary" :style="{ display: buttonVisible }" @click="clickSetTemperature(item.number)">
                设定
              </el-button>

            </div>
          </el-col>
        </el-row>
      </el-card>
    </el-card>
    <el-dialog
      title="温度设定"
      :visible.sync="dialogVisible"
      width="30%"
    >
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="上限温度">
          <el-input v-model="form.maxTem"></el-input>
        </el-form-item>
        <el-form-item label="下限温度">
          <el-input v-model="form.minTem"></el-input>
        </el-form-item>
        <el-form-item label="目标温度">
          <el-input v-model="form.targetTem"></el-input>
        </el-form-item>
      </el-form>
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="clickSave">确 定</el-button>

    </el-dialog>

  </div>
</template>

<script>
import ECharts from 'vue-echarts'
import 'echarts/lib/chart/pie'

import 'echarts/lib/chart/bar'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import 'echarts/lib/component/toolbox'
import Request from "../utils/axiosUtils";
import testTheremo from './theremometer.vue'

export default {
  components: {
    'v-chart': ECharts,
    testTheremo
  },
  name: 'firstPage',
  data() {
    return {
      msg: 'Welcome to Your Vue.js App',
      data: [11],
      currentFurnaceId: 0,
      form: {
        targetTem: '',
        maxTem: '',
        minTem: '',
      },
      colors: [
        {color: '#f56c6c', percentage: 20},
        {color: '#e6a23c', percentage: 40},
        {color: '#5cb87a', percentage: 60},
        {color: '#1989fa', percentage: 80},
        {color: '#6f7ad3', percentage: 100}
      ],
      defaultOptions: {
        layout: {
          height: 200,
          width: 90
        }
      },
      dialogVisible: false,
      buttonVisible: "",
      quantitativeFurnaceArray: [
        // {
        //   number: "1",
        //   temperature: 750,
        //   quality: 500,
        //   maxTem:700,
        //   minTem:620,
        //   targetTem:650,
        // }, 
      ], HoldingfurnaceArray: [
        //   {
        //   number: "1",
        //   temperature: 750,
        //   status: 0,
        // }, 
      ],


    }
  },
  mounted() {
    this.getAllRationFurnaceData();
    this.getAllHoldingFurnaceData();
    setInterval(() => {
      this.getAllHoldingFurnaceData()
    }, 1000 * 3);
    const guestAuthority = this.$cookies.get("guestAuthority");
    if (guestAuthority == undefined) {
      this.$router.push('/LoginPage');
    } else if (guestAuthority == 98) {
      this.buttonVisible = "none";
    }

  },
  methods: {
    clickSetTemperature(index) {
      Request.get("/api/getFurnaceTemperature?furnaceId=" + index + "&furnaceType=0").then((res) => {
        console.log(res)

        this.form.minTem = res.data.message.minTemperature;
        this.form.maxTem = res.data.message.maxTemperature;
        this.form.targetTem = res.data.message.targetTemperature;

      })
      this.dialogVisible = true;
      this.currentFurnaceId = index;
    },
    clickSave() {
      Request.post("/api/saveFurnaceTemperature",
        {
          "maxTemperature": parseInt(this.form.maxTem), "minTemperature": parseInt(this.form.minTem),
          "targetTemperature": parseInt(this.form.targetTem), "furnaceType": 0, "FurnaceId": this.currentFurnaceId
        }
      ).then((res) => {
        console.log(res)
        if (res.data.resCode != 0) return;
      });
      this.dialogVisible = false;
      this.form.minTem = "";
      this.form.maxTem = "";
      this.form.targetTem = "";
      this.getAllHoldingFurnaceData();
    },
    clickSearch() {
      this.$router.push({name: "dataSearch"});

    },
    getTemStyleOptions(temprature, maxTem, minTem) {
      let defaultOptions = {
        text: {
          color: 'black',
          fontSize: 15,
          textAdjustmentY: 0,
          fontFamily: 'Arial',
          textEnabled: true
        },
        layout: {
          height: 240,
          width: 90
        },


      };
      let tempratureInt = parseInt(temprature);
      let maxTempratureInt = parseInt(maxTem);
      let minTempratureInt = parseInt(minTem);
      if (tempratureInt >= maxTempratureInt) {
        defaultOptions["thermo"] = {
          color: "red",
          ticks: 4,
        };
      } else if (tempratureInt < maxTempratureInt && tempratureInt > minTempratureInt) {
        defaultOptions["thermo"] = {
          color: "green",
          ticks: 4,
        };
      } else if (tempratureInt <= minTempratureInt) {
        defaultOptions["thermo"] = {
          color: "blue",
          ticks: 4,
        };
      }

      return defaultOptions;

    },

    getAllHoldingFurnaceData() {
      Request.get("/api/getAllHoldingFurnace").then((res) => {
        
        const mesArray = res.data.message.map(({
                                                 furnaceId: number, currentTemperature: temperature, status,
                                                 maxTemperature: maxTem, minTemperature: minTem, targetTemperature: targetTem
                                               }) =>
          ({number, temperature, status, maxTem, minTem, targetTem})
        );

        this.HoldingfurnaceArray = mesArray;
      });
    },

    getAllRationFurnaceData() {
      Request.get("/api/getAllRationFurnace").then((res) => {

        const mesArray = res.data.message.map(({
                                                 furnaceId: number, currentTemperature: temperature, weight: quality,
                                                 maxTemperature: maxTem, minTemperature: minTem, targetTemperature: targetTem
                                               }) =>
          ({number, temperature, quality, maxTem, minTem, targetTem})
        );

        this.quantitativeFurnaceArray = mesArray;
      });
    },

    // getRealName(number)
    // {
    //   let secName = number;
    //   switch(number/1)
    //   {
    //     case 1: secName = 7;
    //     break;
    //     case 2: secName = 6;
    //     break;
    //     case 3: secName = 26;
    //     break;
    //     case 4: secName = 24;
    //     break;
    //     case 5: secName = 3;
    //     break;
    //     case 6: secName = 4;
    //     break;
    //     case 7: secName = 25;
    //     break;

    //   }   
    //   console.log(secName)   
    //   return secName;
    // }
  }


}
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.mainChar /deep/ .echarts {
  width: 100%;
  height: 100%;
}

.furnaceStatusCls {
  display: inline-block;
  width: 80px;
  height: 80px;
  /*background: red;*/
  border-radius: 50%;
  border: 1px solid #909399;
  margin-top: 70px;

}

.normalColor {
  background: green;
}

.exceptionColor {
  background: red;
  animation: blink 1s infinite steps(1, start);
}

@keyframes blink {
  0%, 100% {
    background-color: red;
  }
  50% {
    background-color: rgb(255, 255, 255); /* not #aaa because it's seem there is Google Chrome bug */

  }
}

.cardCls {
  padding: 0px 50px 50px 0px;
}

.temperfontsize {
  font-size: 30px;
  font-weight:bold;
}
</style>



