<template>
  <div class="changePassPage">
      
    <el-card class="loginCard">
      <div slot="header" class="clearfix">
        <span>用户密码修改</span>
      </div>
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        
        <el-form-item label="旧的密码" prop="pass">
          <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="新的密码" prop="checkPass">
          <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
        </el-form-item>
        
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </el-card>
</div>
</template>


<script>
import Request from "../utils/axiosUtils";
  export default {
    data() {
      
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入旧的密码'));
        } 
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入新的密码'));
        } 
      };
      return {
        ruleForm: {
          pass: '',
          checkPass:''
        },
        rules: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      submitForm() {
        
        const loginName = window.sessionStorage.getItem("loginName");
        Request.post("/api/modifyPassword",{"userAccount":loginName,"userOldPassword":this.ruleForm.pass,
              "userNewPassword":this.ruleForm.checkPass}).then((res)=>{
            console.log(res)
            if(res.data.resCode!=0) return this.$message.error('修改失败');
            this.$message.success('修改成功');
            
          });
        
      },
      resetForm() {
        this.ruleForm.pass='';
        this.ruleForm.checkPass='';
        //this.$refs.ruleFormRef.resetFields();
      }
    }
  }

</script>

<style scoped>
.loginCard {
  background: transparent;
  position: absolute;
  left: 50%;
  top: 50%;


  width: 350px;
  margin: -190px 0 0 -175px;
  border-radius: 5px;
  background: hsla(0, 0%, 100%, 0.3);
  overflow: hidden;
}
</style>


